.footer {
  padding: 60px;
  background: rgba(255, 255, 255, 0.05);
}

.final-cta {
  text-align: right;
  margin-bottom: 60px;
}

.final-cta-button {
  display: flex;
  width: 209px;
  height: 57px;
  padding: 18px 20px;
  justify-content: center;
  align-items: center;
  gap: 9.429px;
}

.social-links {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 40px;
}

.social-links .logo {
  font-size: 24px;
  font-weight: 700;
  color: white;
}

.social-icon {
  color: rgba(255, 255, 255, 0.6);
}

.footer-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 60px;
}

.footer-section h4 {
  color: white;
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 20px;
}

.footer-section a {
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  display: block;
  margin-bottom: 12px;
  font-size: 14px;
}

.footer-bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  padding-top: 30px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
}