.P1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 53px;
  align-self: stretch;
}

.P1-title-container {
  display: flex;
  width: 825px;
  height: 188px;
  justify-content: center;
  align-items: center;
}

.p1-title{
  display: flex;
  width: 825px;
  height: 161px;
  flex-direction: column;
  justify-content: center;
  flex-shrink: 0;

  color: var(--main, #3D38F5);
  text-align: center;
  font-family: MuseoModerno;
  font-size: 229.524px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  letter-spacing: -20.657px;
}

.p1-subtitle{
  color: var(--background, #FEFEFF);
  text-align: center;
  width: 264px;
  height: 10px;
  flex-shrink: 0;

  /* Title1 */
  font-family: Pretendard;
  font-size: 32px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.image-gallery {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 40px;
  padding: 0 60px;
  overflow: hidden;
  width: 100%;
  position: relative;
}

.gallery-container {
  display: flex;
  gap: 20px;
  animation: slideRight 20s linear infinite;
}

@keyframes slideRight {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-2600px);
  }
}

.image-card {
  width: 240px;
  height: 240px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-shrink: 0;
}

.image-placeholder {
  width: 120px;
  height: 120px;
  border-radius: 16px;
}

.gallery-description {
  color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
  text-align: center;
  margin-bottom: 40px;
}

.p1-description {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  align-self: stretch;
}

.p1-description-container {
  display: flex;
  padding: 18px 20px;
  justify-content: center;
  align-items: center;
  gap: 9.429px;
  border-top: 1px solid var(--background, #FEFEFF);
  border-bottom: 1px solid var(--background, #FEFEFF);
}

.description{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  color: var(--background, #FEFEFF);

  /* body1 */
  font-family: Pretendard;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.startBtn {
  margin: 0 auto 120px;
}

