/* Global Styles */
.app {
  width: 1440px;
  height: 3959px;
  margin: 0 auto;
}

/* Button Styles */
.gradient-button {
  border-radius: 30px;
  background: linear-gradient(90deg, #6047AC 0%, #3D38F5 100%);
  border: none;
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.gradient-button::before {
  content: '';
  position: absolute;
  top: -200%;
  left: -300%;
  width: 600%;
  height: 600%;
  background: #3D38F5;
  transition: left 0.3s ease;
  z-index: -1;
  transform: rotate(-30deg);
}

.gradient-button:hover {
  transform: scale(1.2);
}

.gradient-button:hover::before {
  left: -200%;
}

.gradient-button.start-button {
  display: flex;
  width: 209px;
  height: 57px;
  padding: 18px 20px;
  justify-content: center;
  align-items: center;
  gap: 9.429px;
}

/* Text Gradient Styles */
.gradient-text {
  background: linear-gradient(180deg, #E5E4FF 43.27%, #5B57FF 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: MuseoModerno;
  font-weight: 800;
  font-style: normal;
  line-height: normal;
}