.cta-section {
  padding: 80px 60px;
  text-align: center;
}

.cta-title {
  font-size: 32px;
  font-weight: 700;
  color: white;
  margin-bottom: 20px;
  line-height: 1.3;
}

.cta-subtitle {
  font-size: 18px;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 40px;
}

.cta-button {
  margin: 0 auto 60px;
}

.app-preview {
  max-width: 800px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 20px;
  padding: 40px;
  margin-bottom: 120px;
}

.app-preview-placeholder {
  height: 200px;
  background: #f0f0f0;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #999;
}