.moodboard-section {
  padding: 80px 60px;
  text-align: center;
}

.moodboard-title {
  font-size: 24px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 40px;
}

.moodboard-card {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 20px;
  padding: 40px;
  max-width: 600px;
  margin: 0 auto 40px;
  display: flex;
  align-items: center;
  gap: 40px;
}

.moodboard-icon {
  width: 120px;
  height: 120px;
  background: #5B5FFF;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  color: white;
  flex-shrink: 0;
}

.moodboard-content h3 {
  font-size: 32px;
  font-weight: 700;
  color: #333;
  margin-bottom: 20px;
}

.moodboard-tags {
  display: flex;
  gap: 8px;
  margin-bottom: 20px;
}

.tag {
  padding: 8px 16px;
  background: #5B5FFF;
  color: white;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 500;
}

.moodboard-description {
  color: #666;
  font-size: 16px;
  line-height: 1.5;
}