.about-section {
  padding: 80px 60px;
  text-align: center;
}

.about-title {
  font-size: 24px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 40px;
}

.about-main-title {
  font-size: 48px;
  font-weight: 700;
  color: white;
  margin-bottom: 60px;
  line-height: 1.2;
}

.about-main-title .highlight {
  font-size: 53.2px;
  letter-spacing: -4.788px;
}

.about-description {
  font-size: 24px;
  font-weight: 600;
  color: white;
  margin-bottom: 20px;
}

.about-description.moomu-text,
.about-description.infinity-text {
  text-align: center;
  font-family: MuseoModerno;
  font-size: 53.2px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
  letter-spacing: -4.788px;
  margin-bottom: 20px;
}

.about-subdescription {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 80px;
  line-height: 1.5;
}

.features {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-bottom: 120px;
}

.feature-card {
  width: 200px;
  height: 240px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 20px;
  padding: 40px 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.feature-icon {
  width: 80px;
  height: 80px;
  background: #5B5FFF;
  border-radius: 16px;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 36px;
  color: white;
}

.feature-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
}